import React, { useEffect, useState, lazy, Suspense } from 'react';
import PropTypes from 'prop-types';
import { injectMarkdown } from '../../content/markdown.provider';
import { appModule, markdownKeys, resumeRole } from '../../utilities/enums';
import { fetchMarkdownData, getMarkdownContentByKey } from '../../utilities/extensions';
import { Container, Row, Col } from 'react-bootstrap';
import HireConnectWidget from './hire_connect.widget';

function HireMePage(props) {
    const ResumeDocument = lazy(() => import('./resume/resume.document'));
    const resumeMetaData = {
        roleId: resumeRole.FullStackDeveloperDotNet
    }

    return (
        <Container>
            <Row>
                <Col md={{ span: 12, order: 1 }} xs={{ span: 12, order: 2 }} className="mar-bottom-sm">
                    <HireConnectWidget />
                </Col>
                <Col md={{ span: 12, order: 2 }} xs={{ span: 12, order: 1 }} className="d-none d-md-block">
                    <small style={{ color: "red" }}><i>Download Cover Letter for short resume.</i></small>
                    <Suspense fallback={<div><i>Loading Document...</i></div>}>
                        <ResumeDocument {...resumeMetaData} />
                    </Suspense>
                </Col>
            </Row>
        </Container>
    )
}

HireMePage.propTypes = {

};

export default HireMePage;

