import React from 'react';
import PropTypes from 'prop-types';
import { View, Text, Link } from '@react-pdf/renderer';
import { SkillApi } from '../../../data/app-data';
import { markDownTextToPdfTexts } from '../../../utilities/extensions';
import { getKeyByEnumValue, repType } from '../../../utilities/enums';

function ResumeRepositories(props) {
    const getSkillInfo = function (ids) {
        return SkillApi.getSkillByIds(ids);
    }
    return (
        <View>
            <Text style={[props.titleStyle]}>
                Top Repositories
            </Text>
            <View style={[props.styles, { display: "flex", flexDirection: "column", flexWrap: "wrap" }]}>
                {
                    props.data.map((val, idx) => {
                        return (
                            <View wrap={false} style={[props.cardStyle]} key={idx}>
                                <View style={{ borderBottom: 0, marginTop: 2, marginBottom: 3, paddingLeft: 0 }}>
                                    <Text style={[props.headingStyle]}>
                                        {val.title}{" - "}
                                        <Text style={[props.styles.subHeadingStyle, { fontWeight: "normal" }]}>
                                            {getKeyByEnumValue(repType, val.type)}
                                        </Text>
                                    </Text>
                                    <Text style={[props.styles.headingStyle, { fontWeight: "normal", fontStyle: "italic", fontSize: 10 }]}>
                                        {val.sourceUrl}
                                    </Text>
                                </View>
                                <Text style={[props.cardBodyStyle]}>
                                    {markDownTextToPdfTexts(val.shortDescription)}
                                </Text>
                                <View style={[props.cardBodyStyle, { display: "flex", flexDirection: "row", flexWrap: "wrap" }]}>
                                    {getSkillInfo(val.skillIds).map((x, idx) => {
                                        if (x)
                                            return <Text key={idx} style={[props.skillBadge, { fontSize: 10 }]}>
                                                {(x ? x.title : null)}
                                            </Text>;
                                    })}
                                </View>
                            </View>
                        );
                    })
                }
            </View>
        </View >
    )
}

ResumeRepositories.propTypes = {
    styles: PropTypes.object,
    data: PropTypes.array.isRequired
};

export default ResumeRepositories;

