import React from 'react';
import PropTypes from 'prop-types';
import { PDFViewer, Page, Text, View, Document, StyleSheet, Font } from '@react-pdf/renderer';
import ResumeHeader from './resume.header';
import ResumeSkills from './resume.skills';
import { SkillApi, ExperienceApi, RepositoryApi, MetaDataApi } from '../../../data/app-data';
import { appModule } from '../../../utilities/enums';
import ResumeRepositories from './resume.repositories';
import ResumeExperienceView from './resume.experience.view';
import ResumeEducation from './resume.education';
import ResumeSummary from './resume.summary';

// Create styles
// Font.register({ family: 'Open Sans', src: 'https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap' });
Font.register({
    family: 'Roboto', fonts: [
        { src: 'https://raw.githubusercontent.com/abhinav2127/Gitpad/portfolio/font/Roboto-Regular.ttf' },
        { src: 'https://raw.githubusercontent.com/abhinav2127/Gitpad/portfolio/font/Roboto-MediumItalic.ttf', fontStyle: 'italic' },
        { src: "https://raw.githubusercontent.com/abhinav2127/Gitpad/portfolio/font/Roboto-Light.ttf", fontWeight: "light" },
        { src: "https://raw.githubusercontent.com/abhinav2127/Gitpad/portfolio/font/Roboto-Medium.ttf", fontWeight: "semibold" },
        { src: "https://raw.githubusercontent.com/abhinav2127/Gitpad/portfolio/font/Roboto-Bold.ttf", fontWeight: "bold" },
    ]
});

const style = StyleSheet.create(
    {
        font: {
            fontFamily: 'Roboto'
        },
        mainHeading: {
            fontWeight: "semibold",
            color: '#456ea8',
            fontSize: 14,
            marginTop: 15,
            paddingBottom: 1,
            paddingLeft: 2,
            marginBottom: 10,
            borderBottom: 1,
            borderBottomColor: "#2e2e2e",
            fontFamily: 'Roboto'
        },
        subHeading: {
            fontSize: 11,
            marginBottom: 5,
            fontFamily: 'Roboto'
        },
        tableHead: {
            color: '#d05301',
            fontWeight: 500,
            fontSize: 11,
            fontFamily: 'Roboto'
        },
        tableBody: {
            display: "flex",
            flexDirection: "row",
            flexWrap: "wrap",
            fontSize: 11,
            fontFamily: 'Roboto'
        },
        titleStyle: {
            fontWeight: 700,
            fontSize: 11,
            marginBottom: 5,
            fontFamily: 'Roboto'
        },
        badgeStyle: {
            color: "#212529",
            backgroundColor: "#ffaf7a",
            marginRight: 5,
            paddingTop: 3,
            paddingBottom: 2,
            paddingHorizontal: 10,
            borderRadius: 50,
            marginVertical: 1,
            maxHeight: 20
        }
    }
);

const metaData = {
    document: {
        "title": "abhinavJhaResume",
        "author": "Abhinav Jha",
        "subject": "Resume document for job postings",
        "keywords": "Software Developer"
    },
    page: {
        "size": "A4",
        "orientation": "portrait",
        "style": {
            flexDirection: 'column',
            backgroundColor: '#fff',
            // fontFamily: style.font.fontFamily,
            paddingVertical: 30,
            paddingHorizontal: 40
        },
    },
    headerView: {
        fixed: false,
        styles: {
            textAlign: "center",
        },
        titleStyle: [style.mainHeading, { fontSize: 30 }],
        subHeadingStyle: [style.subHeading, { marginTop: 5 }]
    },
    professionalSummary: {
        styles: {
            paddingLeft: 2,
            fontSize: 11,
        },
        titleStyle: style.mainHeading,
    },
    skillView: {
        styles: {
            paddingLeft: 2,
        },
        titleStyle: style.mainHeading,
        tableHeaderStyle: style.tableHead,
        tableBodyStyle: style.tableBody,
        skillBadge: style.badgeStyle
    },
    experienceView: {
        styles: {
            paddingLeft: 2,
        },
        titleStyle: style.mainHeading,
        subHeadingStyle: style.subHeading,
        experienceCardStyles: {
            styles: {
                paddingLeft: 2,
                paddingTop: 5
            },
            titleStyle: style.titleStyle,
            subHeadingStyle: style.subHeading,
            bodyStyle: {
                fontSize: 11,
                fontFamily: "Roboto"
            }
        }
    },
    educationView: {
        styles: {
            paddingLeft: 2,
        },
        titleStyle: style.mainHeading,
        headerStyle: style.titleStyle,
        subHeadingStyle: style.subHeading,
    },
    repositoriesView: {
        styles: {
            paddingLeft: 2,
        },
        titleStyle: style.mainHeading,
        headingStyle: style.titleStyle,
        subHeadingStyle: style.subHeading,
        cardTitleStyle: style.titleStyle,
        cardStyle: {
            display: "flex",
            flexDirection: "column",
        },
        cardBodyStyle: {
            fontSize: style.tableHead.fontSize,
            marginBottom: 5
        },
        skillBadge: style.badgeStyle
    }
};

function ResumeDocument(props) {
    metaData.headerView["personalData"] = MetaDataApi.getPersonData();
    metaData.headerView["contactData"] = MetaDataApi.getContactDetails();
    metaData.skillView["data"] = SkillApi.getSkillByResumeRoles(props.roleId);
    metaData.repositoriesView["data"] = RepositoryApi.getResumeRepositories(props.roleId);
    metaData.experienceView["experiences"] = ExperienceApi.getOrganizationTimeline();
    metaData.experienceView["totalExperience"] = ExperienceApi.getTotalExperience(appModule.Organization);
    metaData.educationView["data"] = ExperienceApi.getEducationTimeline()[0];
    metaData.professionalSummary["data"] = MetaDataApi.getResumeProfessionalSummary(props.roleId);
    return (
        <PDFViewer className="resume-iframe">
            <Document {...metaData.document}>
                <Page {...metaData.page}>
                    <ResumeHeader {...metaData.headerView} />
                    <ResumeSummary {...metaData.professionalSummary} />
                    <ResumeSkills {...metaData.skillView} />
                    <ResumeEducation {...metaData.educationView} />
                    <ResumeExperienceView {...metaData.experienceView} />
                    <ResumeRepositories {...metaData.repositoriesView} />
                </Page>
            </Document>
        </PDFViewer>
    )
}

ResumeDocument.propTypes = {
    roleId: PropTypes.number.isRequired
};

export default ResumeDocument;

