import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { injectMarkdown } from '../../../content/markdown.provider';
import { appModule, markdownKeys } from '../../../utilities/enums';
import { fetchMarkdownData, getMarkdownContentByKey, covertToDate, markDownTextToPdfTexts } from '../../../utilities/extensions';
import { View, Text } from '@react-pdf/renderer';

function ResumeExperience(props) {
    const [resumeText, setResumeText] = useState("");
    const [cvText, setCvText] = useState("");
    useEffect(() => {
        injectMarkdown(appModule.Organization, props.experience.organization.descriptionFile).then(res => {
            fetchMarkdownData(res.default).then(text => {
                setResumeText(markDownTextToPdfTexts(getMarkdownContentByKey(text, markdownKeys.Resume)));
                setCvText(markDownTextToPdfTexts(getMarkdownContentByKey(text, markdownKeys.Cv)));
            });
        });
    }, [resumeText]);
    return (
        <View style={[props.styles.styles, { display: "flex", flexDirection: "column" }]} wrap={false}>
            <View style={{ display: "flex", flexDirection: "row" }}>
                <View>
                    <Text style={[props.styles.titleStyle]}>
                        {props.experience.organization.name}{" - "}
                        <Text style={[props.styles.subHeadingStyle, { fontWeight: "normal" }]}>
                            {props.experience.organization.location}
                        </Text>
                    </Text>
                    <Text style={[props.styles.subHeadingStyle, { fontWeight: 700 }]}>
                        {props.experience.organization.keyAreas[0]}
                    </Text>
                </View>
                <View style={{ width: 500, alignItems: "flex-end" }}>
                    <Text style={[props.styles.subHeadingStyle]}>
                        {covertToDate(props.experience.fromDate)} {" - "}
                        {(props.experience.isWorking) ? "Present" : covertToDate(props.experience.toDate)}
                    </Text>
                </View>
            </View>
            <View style={[props.styles.bodyStyle]}>
                <Text >
                    {resumeText != "" ? resumeText : " "}
                </Text>
                <Text style={{ fontWeight: "bold", color: props.styles.titleStyle.color, marginTop: 10, marginBottom: 5 }}>
                    Key Skills
                </Text>
                <Text >
                    {cvText != "" ? cvText : " "}
                </Text>
            </View>
        </View>
    )
}

ResumeExperience.propTypes = {
    styles: PropTypes.object.isRequired,
    experience: PropTypes.object.isRequired,
    totalExperience: PropTypes.object.isRequired
};

export default ResumeExperience;

