import React from 'react';
import PropTypes from 'prop-types';
import { View, Text } from '@react-pdf/renderer';
import ResumeExperience from './resume.experience';

function ResumeExperienceView(props) {
    return (
        <View>
            <View style={[props.titleStyle, { display: "flex", flexDirection: "row", marginBottom: 0 }]}>
                <Text >
                    Experience
                    </Text>
                <Text style={[props.subHeadingStyle, { fontStyle: "italic", alignSelf: "flex-end", textAlign: "right", marginRight: 70, color: "black", marginBottom: 0 }]}>
                    {props.totalExperience.totalExperience}
                </Text>
            </View>
            {

                props.experiences.map((val, idx) => {
                    let expProps = {
                        styles: props.experienceCardStyles,
                        experience: val,
                        totalExperience: props.totalExperience
                    }
                    return (<ResumeExperience {...expProps} key={idx} />);
                })
            }
        </View>
    )
}

ResumeExperienceView.propTypes = {
    experiences: PropTypes.array.isRequired,
    totalExperience: PropTypes.object.isRequired,
    styles: PropTypes.object.isRequired
};

export default ResumeExperienceView;

