import React from 'react';
import PropTypes from 'prop-types';
import { View, Text, Link } from '@react-pdf/renderer';

//| {props.contactData.phoneNumber}
function ResumeHeader(props) {

    return (
        <View fixed={props.fixed} style={props.styles}>
            <Text style={props.titleStyle}>
                {props.personalData.name}
            </Text>
            <Text style={[props.subHeadingStyle, { marginTop: 0 }]}>
                {props.contactData.email} | Medium: {props.contactData.mediumTag} | LinkedIn: {props.contactData.linkedInTag} | Twitter: {props.contactData.twitterTag}
            </Text>
        </View>
    )
}

ResumeHeader.propTypes = {
    fixed: PropTypes.bool.isRequired,
    styles: PropTypes.object.isRequired,
    personalData: PropTypes.object.isRequired,
    contactData: PropTypes.object.isRequired
};

export default ResumeHeader;

