import React from 'react';
import { Modal, Button } from 'react-bootstrap';

function CompanyConnectWidget(props) {
    const [height, setHeight] = React.useState(window.innerHeight);
    return (
        <Modal
            show={props.show}
            onHide={props.onHide}
            backdrop="static"
        >
            <Modal.Body>
                <small><b>Please leave your details so that I can directly connect on call.</b></small>
                <br />
                <iframe src="https://docs.google.com/forms/d/e/1FAIpQLScIcmw3rNrXp1I2auEMpTLdOjViIkApkCkcJk4a1yNrTvI7Og/viewform?embedded=true" width="100%" height={height - 180} frameborder="0" marginheight="0" marginwidth="0">Loading</iframe>
            </Modal.Body>
            <Modal.Footer>
                <Button variant="danger" onClick={props.onHide}>
                    Skip
                </Button>
            </Modal.Footer>
        </Modal>
    )
}

export default CompanyConnectWidget;

